import "./ComboCard.css";
function ComboCard(props) {
  return (
    <>
      <div className="full_card">
        <p className="card_head">{props.cardhead}</p>
        <div className="two_image_card">
          <div>
            <img className="card_image" src={props.Image1} alt="none " />
            <p className="card_image_title">Acs</p>{" "}
          </div>
          <div>
            <img className="card_image" src={props.Image2} alt="none " />
            <p className="card_image_title">Refrigerator</p>
          </div>
        </div>
        <div className="two_image_card">
          <div>
            <img className="card_image" src={props.Image3} alt="none " />
            <p className="card_image_title">Acs</p>{" "}
          </div>
          <div>
            <img className="card_image" src={props.Image4} alt="none " />
            <p className="card_image_title">Refrigerator</p>
          </div>
        </div>{" "}
        <br />
        <p className="seemore">See more</p>
      </div>
    </>
  );
}
export default ComboCard;
