import "./BannerCardComponent.css";
function BannerCardComponent(props) {
  return (
    <>
      <div className="mainCardbox">
        <img src={props.Image1} alt="" />
        <img src={props.Image2} alt="" />
        <img src={props.Image3} alt="" />
        <img src={props.Image4} alt="" />
        <img src={props.Image5} alt="" />
        <img src={props.Image6} alt="" />
        <img src={props.Image7} alt="" />
      </div>
    </>
  );
}
export default BannerCardComponent;
